import React, { Component } from 'react'
import classes from './navmenu.module.css'
import Logo from './LogoforSite.png'


class NavMenu extends Component {

 state={
  buttonHome:classes.Buttons,
  buttonPhoto:classes.Buttons,
  linkHome:classes.navLinks,
  linkPhoto:classes.navLinks
 }

 buttonHome= () => {
  this.setState({
   buttonHome:classes.Buttons,
   buttonPhoto:classes.Buttons,
   linkHome:classes.navLinks,
   linkPhoto:classes.navLinks,
  })
 }

 buttonPhoto= () => {
  this.setState({
   buttonHome:classes.Buttons,
   buttonPhoto:classes.ButtonsActive,
   linkHome:classes.navLinks,
   linkPhoto:classes.navLinksActive,

  })
 }
 render() {
  return (
   <div className={classes.NavMenu}>
    <img className={classes.LogoImg} src={Logo} alt="Logo" />

    <ul className={classes.NavButtons}>
     <li className={this.state.buttonHome} onClick={this.buttonHome}>
      <a href="/" className={this.state.linkHome}><h3>HOME</h3></a>
     </li>
     <li className={this.state.buttonPhoto} onClick={this.buttonPhoto}>
      <a href="/photo" className={this.state.linkPhoto}><h3>PHOTO</h3></a>
     </li>
     <li className={classes.Buttons}>
      <h3>VIDEO</h3>
     </li>
     <li className={classes.Buttons}>
      <h3>MEETINGS</h3>
     </li>
    </ul>
    <div className={classes.langButtons}>
     RU
     </div>
   </div>
  )

 }
}


export default NavMenu;